  background: none;
  border: none;
  border-bottom: 1px solid #333;
  width: 100%;
  padding: 10px 10px 10px 5px;
  font-size: 16px;
  transition: border-bottom .2s, color .2s;
  display: block;
}

.wave-group .input:hover {
  cursor: pointer;
}

.wave-group .input:focus {
  border-bottom: 1px solid #344f10;
  outline: none;
}

.wave-group .label {
  color: #333;
  pointer-events: none;
  font-size: 18px;
  font-weight: normal;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 10px;
  left: 5px;
}

.label-char {
  font-size: 14px;
  transition: all .2s;
}

.index-0 {
  transition-delay: 0s;
}

.index-1 {
  transition-delay: 50ms;
}

.index-2 {
  transition-delay: .1s;
}

.index-3 {
  transition-delay: .15s;
}

.wave-group .input:focus ~ label .label-char, .wave-group .input:valid ~ label .label-char {
  color: #344f10;
  cursor: pointer;
  font-size: 14px;
  transform: translateY(-20px);
}

.wave-group .bar {
  width: 100%;
  margin-top: 5px;
  display: block;
  position: relative;
}

.wave-group .input:focus ~ .bar:before, .wave-group .input:focus ~ .bar:after, .wave-group .input:valid ~ .bar:after {
  width: 50%;
}

